import React,{useState , useEffect} from 'react'
import { Col,Row , Modal , Button } from 'react-bootstrap'
import deleteicon from '../../images/delete.png'
import { useDispatch, useSelector } from 'react-redux'
import { DELETE_Cart_ID, PUT_Cart_ID } from '../../redux/S_Cart'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const CartItem = ({ e }) => {
  const [showDelete, setshowDelete] = useState(false);
  const [quantity , setquantity] = useState()
  const [isload , setisload] = useState(true)
  const dispatch = useDispatch()
  useEffect(_ => setquantity(e && e.quantity), [])
  const CartPut = useSelector(state=> state.SliceCart.CartPut)
  const handleDelete = () => {
    dispatch(DELETE_Cart_ID(e._id))
    toast.success("    ")
    setTimeout(() => {
      window.location.reload(false)
    }, 500);
  }

  const handleQuantity = async () => {
    setisload(true)
    await dispatch(PUT_Cart_ID({id:e&& e._id,  quantity: quantity }))
    setisload(false)
    toast.success("  ")
    // window.location.reload(false)
  }


    return (
      <Col xs="12" className="cart-item-body my-2 d-flex px-2">

              {/* For Delete */}
      <Modal show={showDelete}  onHide={_=> setshowDelete(!showDelete)}>
        <Modal.Header> <Modal.Title> <div className="font">  </div>{" "} </Modal.Title> </Modal.Header>
        <Modal.Body> <div className="font">     </div>{" "} </Modal.Body>
        <Modal.Footer>
          <Button className="font" variant="success" onClick={_=> setshowDelete(!showDelete)} ></Button>
          <Button className="font" variant="success" onClick={handleDelete} ></Button>
        </Modal.Footer>
      </Modal>



        {
          e && e.product && <img width="200px" style={{padding:"10px"}} height="197px" src={e.product.imageCover} alt="" />
        }
        
        <div className="w-100">
          <Row className="justify-content-between">
            <Col sm="12" className=" d-flex flex-row justify-content-between">
              <div className="d-inline pt-2 cat-text"></div>
              <div className="d-flex pt-2 " style={{ cursor: "pointer" }} onClick={_=> setshowDelete(!showDelete)}>

                <img src={deleteicon} alt="" width="20px" height="24px" />
                <div className="cat-text d-inline me-2"></div>


              </div>
            </Col>
          </Row>
          <Row className="justify-content-center mt-2">
            <Col sm="12" className=" d-flex flex-row justify-content-start">
            {
              e && e.product && <div className="d-inline pt-2 cat-title">{e.product.title}</div>
           }
              <div className="d-inline pt-2 cat-rate me-2">4.5</div>
            </Col>
          </Row>
          <Row>
            <Col sm="12" className="mt-1 d-flex">
              <div className="cat-text d-inline">     :</div>
              <div
                className="color ms-2 border"
                style={{ backgroundColor: e&&e.color}  }>  </div>
            </Col>
          </Row>
  
          <Row className="justify-content-between">
            <Col sm="12" className=" d-flex flex-row justify-content-between">
              <div className="d-inline pt-2 d-flex">
                {/* <div className="cat-text  d-inline"></div>
                <input
                  onChange={e=> setquantity(e.target.value)}
                  value={quantity}
                  className="me-2 edite3" type="number" />
                <button className="copon-btn d-inline " onClick={handleQuantity}></button> */}

              </div>
              {
              e&&e.product && e.product.priceAfterDiscount
                ? <div className="product-price d-inline px-3 py-3 border"> <span className='palance1'>{e.price * e.quantity}</span>  {e.product.priceAfterDiscount * e.quantity} </div>
                : <div className="product-price d-inline px-3 py-3 border">{e.price} </div>
            }
              {/* <div className="d-inline pt-2 barnd-text">{e&& e.price} </div> */}
            </Col>
          </Row>
        </div>
        <ToastContainer />
      </Col>
    )
}

export default CartItem
