import React from 'react'
import { Row, Col } from 'react-bootstrap'
import mobile from '../../images/mobile.png'
import Loading from '../../util/Loading'
import UserAllOrderCard from './UserAllOrderCard'
const UserAllOrderItem = ({ e, index, }) => {
    return (
        <div className="user-order mt-2">
            <Row>
                <div className="py-2 order-title">   # { index +1} </div>
            </Row>
            {
                e && e.cartItems
                    ?  e.cartItems.map((item , index)=> <UserAllOrderCard key={index} item ={item} />)
                    : <Loading />
            }

            <Row className="d-flex justify-content-between">
                <Col xs="6" className="">
                    <div>
                        <div className="d-inline"></div>
                        <div className="d-inline mx-2 stat"> {
                        e.isDelivered == true ? " " : "   "
                        }</div>
                        <div className="d-inline"></div>
                        <div className="d-inline mx-2 stat">{
                        e.isPaid == true ? " " : "   "
                        }  </div>
                        <div className="d-inline">  </div>
                        <div className="d-inline mx-2 stat"> </div>
                    </div>
                </Col>
                <Col xs="6" className="d-flex justify-content-end">
                    <div>
                        <div className="barnd-text">{e.totalOrderPrice} </div>
                    </div>
                </Col>
            </Row>
        </div>
    )
}

export default UserAllOrderItem
